import axios from "axios";
import modal from "./modal";
import router from '@/router';

export default {

  baseUrl: '',

  //token: '',

  init(params){
    if(params.baseUrl) this.baseUrl = params.baseUrl
    else if(params.token) sessionStorage.setItem("token", params.token)
  },

  defaultHeadData: {
    headers: {}
  },

  defaultSucHandler: {
    errorHandle(response) {
      if (response.code == 200) {
        return this.service
      } else return null
    },

    service: {
      _1001(res) {
        sessionStorage.clear()
        router.replace('/login')
      },
      _others(errorMsg) {
        modal.alert(errorMsg)
      }
    },
  },

  defaultFailHandler() {
    modal.alert('网络请求失败')
  },

  request(requestData) {
    //
    let mergeData = Object.assign({}, this.defaultHeadData, requestData.head)
    //
    //mergeData.header = Object.assign({}, {}, mergeData.header)
    //
    mergeData.url = this.baseUrl + requestData.head.url
    //
    if(mergeData.method.toLowerCase() == 'get') mergeData.params = requestData.data
    else mergeData.data = requestData.data
    //加上token
    if(sessionStorage.getItem("token")) mergeData.headers.Authorization = sessionStorage.getItem("token")

    //success解析
    if (requestData.success == null) {
      requestData.success = (res) => {
        //let defaultHandler = this.defaultSucHandler.errorHandle(res);

        let errorFunName = '_' + res.code

        if (requestData[errorFunName] != null) {
          return requestData[errorFunName](res.data, res)
        } else if (this.defaultSucHandler.service[errorFunName] != null) {
          return this.defaultSucHandler.service[errorFunName](res)
        }else {
          return this.defaultSucHandler.service._others(res.msg)
        }
      }
    }
    //fail解析
    if (requestData.fail == null) {
      requestData.fail = (res) => {
        return this.defaultFailHandler(res);
      }
    }

    let responseHandler = (response) => {
      return new Promise((resolve, reject) => {
        if(response.status == 200){
          resolve(requestData.success(response.data))
        } else {
          reject(requestData.fail(response))
        }
      })
    }
    //
    return axios.request(mergeData).then(responseHandler)
  }
}



