import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

//以下代码解决路由地址重复点击的报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const originalPushs = VueRouter.prototype.replace
VueRouter.prototype.replace = function replace(location) {
  return originalPushs.call(this, location).catch(err => err)
}

export default new VueRouter({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/components/Login')
    },
    {
      path:'/',
      name: 'Home',
      component: () => import('@/components/Home'),
      children: [
        {
          path: '',
          name: 'Default',
          component: () => import('@/components/case/CaseList'),
        },
        {
          path: 'case',
          name: 'CaseList',
          component: () => import('@/components/case/CaseList'),
        },
        {
          path: 'case/:caseId/treatment/save',
          name: 'TreatmentSave',
          component: () => import('@/components/case/TreatmentSave'),
        },
        {
          path: 'case/:caseId/treatment-record',
          name: 'TreatmentRecord',
          component: () => import('@/components/case/TreatmentRecord'),
        },
        {
          path: 'case/save',
          name: 'CaseAdd',
          component: () => import('@/components/case/CaseSave'),
        },
        {
          path: 'case/:caseId/save',
          name: 'goodsUpdate',
          component: () => import('@/components/case/CaseSave'),
        },
        {
          path: 'prescription',
          name: 'PrescriptionList',
          component: () => import('@/components/prescription/PrescriptionList'),
        },
        {
          path: 'prescription/save',
          name: 'PrescriptionAdd',
          component: () => import('@/components/prescription/PrescriptionSave'),
        },
        {
          path: 'prescription/:psId/save',
          name: 'PrescriptionUpdate',
          component: () => import('@/components/prescription/PrescriptionSave'),
        }
      ]
    }
  ]
})



