import router from '@/router';

export default {
  _data: {
    psList: [
      {
        psId: 1,
        psName: '抑郁症',
        psAnode: '脑患侧半球口舌区',
        psCathode: '对侧框额叶',
        psCurrent: 1000,
        psDuration: 20
      }
    ],
    caseList: [
      {
        caseId: 1,
        name: "张三",
        phoneNo: 13976998433,
        age: 22,
        sex: 1,
        height: 170,
        weight: 55.2,
        startDate: "2023-08-14",
        treatmentNum: 0,
      }
    ],
    treatmentList: [
      {
        tmId: 1,
        caseId: 1,
        presetPsId: 1,
        anode: '脑患侧半球口舌区',
        cathode: '对侧框额叶',
        current: 1000,
        duration: 20,
        cycle: '',
        beginInterval: '20:00:00',
        endInterval: '21:00:00'
      }
    ],
    treatmentRecordList: [
      {
        recordId: 1,
        caseId: 1,
        tmId: 1,
        treatDate: "2023-08-15",
        treatTime: "21:00:01",

        anode: '脑患侧半球口舌区',
        cathode: '对侧框额叶',
        current: 1000,
        duration: 20
      }
    ],
  },

  init() {
    const data = sessionStorage.getItem('TempData')
    if(data) {
      this._data = JSON.parse(data)
    }
  },

  auth() {
    const token = sessionStorage.getItem("token")
    if(!token) {
      router.replace('/login')
      return false
    }
    return true
  },

  psList() {
    return this._data.psList
  },
  ps(psId) {
    return Object.assign({}, this._data.psList[psId - 1])
  },
  deletePs(psId) {
    this._data.psList.splice(psId - 1, 1)
    sessionStorage.setItem('TempData', JSON.stringify(this._data))
  },
  savePs(psData) {
    if(psData.psId) {
      this._data.psList[psData.psId - 1] = psData
    } else {
      psData.psId = this._data.psList.length + 1
      this._data.psList.push(psData)
    }
    sessionStorage.setItem('TempData', JSON.stringify(this._data))
  },

  caseList() {
    this.auth()
    return this._data.caseList
  },
  case(caseId) {
    return Object.assign({}, this._data.caseList[caseId - 1] )
  },
  deleteCase(caseId) {
    this._data.caseList.splice(caseId - 1, 1)
    sessionStorage.setItem('TempData', JSON.stringify(this._data))
  },
  saveCase(caseData) {
    if(caseData.caseId) {
      this._data.caseList[caseData.caseId - 1] = caseData
    } else {
      caseData.caseId = this._data.caseList.length + 1
      caseData.startDate = this.getCurrentDate()
      this._data.caseList.push(caseData)
    }
    sessionStorage.setItem('TempData', JSON.stringify(this._data))
  },

  treatmentRecordList(caseId) {
    return this._data.treatmentRecordList.filter(record=> record.caseId == caseId)
  },

  newestTreatmentFromCase(caseId) {
    const tm = this._data.treatmentList.filter(tm=> tm.caseId == caseId).reverse()[0]
    return tm ? Object.assign({}, tm) : null
  },
  treatment(tmId) {
    return Object.assign({}, this._data.treatmentList[tmId - 1])
  },
  saveTreatment(tmData) {
    if(tmData.tmId) {
      this._data.treatmentList[tmData.tmId - 1] = tmData
    } else {
      tmData.tmId = this._data.treatmentList.length + 1
      this._data.caseList[tmData.caseId - 1].treatmentNum = 0
      this._data.treatmentList.push(tmData)
    }
    sessionStorage.setItem('TempData', JSON.stringify(this._data))
  },


  getCurrentDate() {
    var date = new Date()
    var seperator1 = "-"
    var year = date.getFullYear()
    var month = date.getMonth() + 1
    var strDate = date.getDate()
    if (month >= 1 && month <= 9) {
      month = "0" + month
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = "0" + strDate
    }
    return year + seperator1 + month + seperator1 + strDate
  },
}



